}

.logo:before {
    position: absolute;
    background-color: inherit;
    content: '';
    width: 1000px;
    height: 100%;
    transform: skew(-35deg);
    z-index: 0;
    top: 0;
    right: -260px;
    height:107px;
}

.logo img {
