.search-bar {
	position: relative;
	width: 0%;
	min-width: 70px;
	height: 75px;
	
	overflow: hidden;
	transition: width 0.3s;
	display: inline-block;
}
.search-bar-input {
	border: none;
	outline: none;
	background: #fff;
	width: 80%;
	height: 45px;
	margin: 0;
	padding: 0px 52px 0px 20px;
	font-size: 14px;
	color: #bebebe;
	border-bottom: 1px #F3547D solid;
	border-left: 0px #E8E8E8 solid
}
.search-icon, .search-bar-submit {
	width: 70px;
	height: 75px;
	display: block;
	position: absolute;
	right: 0;
	top: 0;
	padding: 0;
	margin: 0;
	line-height: 42px;
	text-align: center;
	cursor: pointer;
	border-left: 1px solid #e5e5e5;
}
.search-bar-submit {
	background: #fff; /* IE needs this */
	opacity: 0;
	color: transparent;
	border: none;
	outline: none;
	z-index: -1;
}
.search-icon:before {
	content: '\f002';

	font-size: 18px;
	color:#333
}

.navbar-form {padding:0px; margin:0px}

.navbar-collapse {float:right; padding:0px; margin-left:25px}

.navbar-form .search-btn {
	color: #fff;
	background:none;
	z-index: 90;

	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none;
	border: none;
	outline: none;
	width: 76px;
	height: 65px;
	display: block;
	right: 0;
	top: 0;
	padding: 0;
	margin: 0;
	line-height: 65px!important;
	text-align: center;
	cursor: pointer;
	border-radius:0;
}
.navbar-form .search-btn      {line-height: 65px!important;vertical-align:middle;}
.navbar-form .search-btn     -search:before {
    font-size: 18px;
	color:#121212;
	vertical-align:middle
}

.navbar-collapse form[role="search"] input {

    font-size: 18pt;
    opacity: 0;
    display: none;
    height: 48px;
	    position: relative;
    z-index: 2;
    float: left;
}

.navbar-collapse form[role="search"].active input {
display: table-cell;
    opacity: 1;
    z-index: 100;
    border-radius: 0;
    border: none;
    line-height: 45px;
    height: 67px;
    font-size: 14px;
    padding: 0px 25px;
    width: 300px;
    position: absolute;
    right: 75px;
	border-left:1px #e5e5e5 solid;
	box-shadow:none
}

/* Open state */
